import React, {Component} from 'react';

class Experience extends Component{
	render(){
		return(
			<div className="def">
				<div>
					<div className="about-desc" >
						<span className="about-head">Experience</span>
						<h2 className="about-heading">Work Experience</h2>
					</div>
				</div>
				<div className="timeline-label">
					<h2 className="color-1 titles"><i className="icon-pen2">  </i>Full Stack Developer <span className="date">2017 till date</span></h2>
					<p className="facts">
						Experienced FreeLance Full Stack Developer with knowledge of Frontend and Backend technologies
						HTML5, CSS3, XML, JavaScript, JQuery, React, React Native, Express, Node, Python, Django,
						PHP, MySQL, MongoDB, Game Development, etc
					</p>
				</div>

				<div className="timeline-label">
					<h2 className="color-2 titles"><i className="icon-pen2">  </i>ICT Manager  <span className="date">2018-2019</span></h2>
					<p className="facts">
						ICT Manager during NYSC at the Centre For Child Care and Youth Development
						Located at Sabon Lugbe, Lugbe, Airport Road, Abuja, Nigeria.
					</p>
				</div>

				<div className="timeline-label">
					<h2 className="color-3 titles"><i className="icon-pen2">  </i>Graphics Designer <span className="date">2017 till date</span></h2>
					<p className="facts">
						FreeLance Graphics Designer skilled in the use of modern softwares including
						Adobe PhotoShop, Corel Draw etc.
					</p>
				</div>

				<div className="timeline-label">
					<h2 className="color-4 titles"><i className="icon-pen2">  </i>Electronics Engineer <span className="date">2014 till date</span></h2>
					<p className="facts">
						FreeLance Electronics circuits Designs and installations skilled in the use of
						Arduino's, Raspberries, Micro-Controllers and other components.
						Has Knowledge of Assembly Language.
					</p>
				</div>

				<div className="timeline-label">
					<h2 className="color-5 titles"><i className="icon-pen2">  </i>Equipments Engineer <span className="date">2014-2014</span></h2>
					<p className="facts">
						Industrial Training at the Anambra State Broadcasting Service (ABS), Awka, Anambra
						State, Nigeria.
						Knowledge of inverters, automated switching etc.
					</p>
				</div>
			</div>
		)
	}
}

export default Experience;