import React from 'react';

const Contact = () =>(
	<div className="contacted">
		<div className="def">
			<div>
				<div className="about-desc" >
					<span className="about-head">Get in Touch</span>
					<h2 className="about-heading">Contact</h2>
				</div>
			</div>
			
		</div>
		<div className="form-data">
			<p className="inline"><i className="icon-globe-outline"></i><span className="contact-details">admin@jequex.com.ng</span></p>				

			<p className="inline space"><i className="icon-phone-outline"></i><span className="contact-details">+234 814 167 6170</span></p>

			<p><i className="icon-map"></i><span className="contact-details">16 Allan Ekwerekwu street, inland town, 3-3, Onitsha</span></p>
			

			<form action="">
				<div className="form-group">
					<div className="form-group1">
						<div className="form-group2">
							<input type="text" className="form-control" placeholder="Name"></input>
							<input type="text" className="form-control" placeholder="Email"></input>
							<input type="text" className="form-control" placeholder="Subject"></input>
						</div>
						<div>
							<textarea name="" id="message" cols="24" rows="4" className="form-control" placeholder="Message"></textarea>
						</div>
					</div>
					<div className="form-group3">
						<button className="button1">send message</button>
					</div>
				</div>
			</form>
		</div>
	</div>
)

export default Contact;